.kui-theme--light {
  --color-metadata-code-bg: #f8f9fa; }

.kui-theme--dark {
  --color-metadata-code-bg: #061c28; }

.pipeline-metadata-code {
  position: absolute;
  top: -1px;
  /* Avoids pixel rounding gaps */
  right: 0;
  bottom: -1px;
  left: 0;
  z-index: 2;
  display: flex;
  flex-direction: column;
  padding: 0 400px 0 0;
  background: var(--color-metadata-code-bg);
  transform: translateX(100vw);
  transition: transform ease 0.5s 0.1s, left ease 0.5s; }
  .pipeline-metadata-code--visible {
    transform: translateX(0); }
  @media (min-width: 1200px) {
    .pipeline-metadata-code--sidebarVisible {
      left: 424px; } }
  @media (min-width: 1304px) {
    .pipeline-metadata-code--sidebarVisible {
      left: calc( 100% - 904px); } }
  @media (min-width: 1200px) {
    .pipeline-metadata-code--no-sidebarVisible {
      left: 80px; } }
  @media (min-width: 960px) {
    .pipeline-metadata-code--no-sidebarVisible {
      left: calc( 100% - 904px); } }
  @media print {
    .pipeline-metadata-code {
      display: none; } }

.pipeline-metadata-code__title {
  flex-grow: 0;
  margin: 2.1em 36px 1.8em 36px;
  font-weight: normal;
  font-size: 1.7em;
  line-height: 1.6; }

.pipeline-metadata-code__code {
  display: block;
  flex-grow: 1;
  overflow: auto;
  font-size: 1.25em;
  line-height: 1.8;
  opacity: 1;
  transition: opacity 0.4s ease 0.4s; }
  .pipeline-metadata-code--no-visible .pipeline-metadata-code__code {
    opacity: 0;
    transition: opacity 0.3s ease; }
  .pipeline-metadata-code__code * {
    font-family: Consolas, Monaco, 'Courier New', Courier, monospace; }
  .pipeline-metadata-code__code .hljs-keyword {
    color: var(--color-metadata-code-text); }

.pipeline-metadata-code pre {
  display: inline-block;
  margin: 0;
  padding: 0 36px 36px; }

.pipeline-metadata__code-toggle {
  transition: opacity 0.2s ease; }
  .pipeline-metadata__code-toggle--no-hasCode {
    opacity: 0.35;
    pointer-events: none; }

.pipeline-metadata__code-toggle {
  margin-top: 0.3em;
  margin-bottom: 2em; }

.pipeline-metadata__code-toggle-input {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  clip-path: inset(50%); }

.pipeline-metadata__code-toggle-label {
  position: relative;
  padding: 0 0 0 2.8em;
  cursor: pointer;
  user-select: none; }
  .pipeline-metadata__code-toggle-label:before, .pipeline-metadata__code-toggle-label:after {
    position: absolute;
    top: 0.81em;
    left: 0;
    display: block;
    margin: 0 0.8em 0 0;
    transform: translateY(-50%);
    content: ' '; }
  .pipeline-metadata__code-toggle-label:before {
    width: 1.7em;
    height: 0.8em;
    background-color: rgba(204, 209, 214, 0.3);
    border-radius: 0.5em;
    transition: opacity 0.15s ease; }
  .pipeline-metadata__code-toggle-label:after {
    width: 0.8em;
    height: 0.8em;
    background-color: rgba(204, 209, 214, 0.7);
    border-radius: 50%;
    transition: opacity 0.15s ease, transform 0.15s ease; }
  [data-whatinput='keyboard'] input:focus + .pipeline-metadata__code-toggle-label:before {
    box-shadow: 0 0 0 3px rgba(0, 176, 245, 0.85); }
  .pipeline-metadata__code-toggle-label:hover:after {
    background-color: #ccd1d6; }
  .pipeline-metadata__code-toggle-label--checked:before {
    background-color: rgba(0, 176, 245, 0.3); }
  .pipeline-metadata__code-toggle-label--checked:after {
    background-color: rgba(0, 176, 245, 0.7);
    transform: translate(100%, -50%); }
  .pipeline-metadata__code-toggle-label--checked:hover:after {
    background-color: #00b0f5; }
